<template>
  <v-container grid-list-xs>
    <v-overlay :value="overlay">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
      <v-btn icon @click="overlay = false">
        <v-icon>mdi-close</v-icon>
      </v-btn>
    </v-overlay>
    <v-overlay :value="overlayUnclosed">
      <v-progress-circular
          color="primary"
          indeterminate
          size="50"
      ></v-progress-circular>
    </v-overlay>
    <v-navigation-drawer
      app
      class="primary darken-1 white--text"
      clipped
      permanent
      right
      style="z-index: 3;"
    >
      <v-list class="white--text">
        <v-list-item>
          <v-btn class="secondary" dark @click="$router.push('/resource/view/applicant/' + practitionerId)">
            <v-icon light left>mdi-eye</v-icon>
            <span>View Health Profession</span>
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-btn
            :disabled="!canAddShortlist"
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/interview-shortlist/interview-shortlist?practitioner=' + practitionerId + '&application=' + queries.id)"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Interview Shortlist</span>
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-btn
            :disabled="!canAddAssessment"
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/post-interview/post-interview?practitioner=' + practitionerId + '&application=' + queries.id)"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Interview Assessments</span>
          </v-btn>
        </v-list-item>
        <v-list-item>
          <v-btn
            :disabled="!canAddHiring"
            class="primary"
            dark
            small
            @click="$router.push('/questionnaire/hiring-decision/hiring-decision?practitioner=' + practitionerId + '&application=' + queries.id)"
          >
            <v-icon light left>mdi-plus</v-icon>
            <span>Hiring Decision</span>
          </v-btn>
        </v-list-item>
      </v-list>
    </v-navigation-drawer>
    <v-row>
      <v-col>
        <bw-page-view :id="queries.id" page="application" />
        <bw-page-view v-if="shortlist.id" :id="shortlist.id" page="interview-shortlist" />
        <bw-page-view v-if="assessment.id" :id="assessment.id" page="post-interview" />
        <bw-page-view v-if="hiring.id" :id="hiring.id" page="hiring-decision" />
      </v-col>
    </v-row>
  </v-container>
</template>
<script>
const querystring = require("querystring");
export default {
  props: ["queries"],
  data() {
    return {
      overlay: false,
      overlayUnclosed: false,
      practitionerId: '',
      shortlist: {
        id: ''
      },
      assessment: {
        id: ''
      },
      hiring: {
        id: ''
      }
    }
  },
  methods: {
    getApplication() {
      fetch("/fhir/Basic/" + this.queries.id).then((response) => {
        response.json().then((data) => {
          let practRef = data.extension.find((ext) => {
            return ext.url === "http://ihris.org/fhir/StructureDefinition/ihris-practitioner-reference"
          })
          this.practitionerId = practRef.valueReference.reference.split("/")[1]
          this.getInterviewShortList()
        })
      })
    },
    getInterviewShortList() {
      let params = {
        _profile: "http://ihris.org/fhir/StructureDefinition/interview-shortlist-profile",
        jobapplication: "Basic/" + this.queries.id,
        practitioner: this.practitionerId
      }
      fetch("/fhir/Basic?" + querystring.stringify(params)).then(response => response.json().then((shortlist) => {
        if(shortlist.entry && shortlist.entry.length > 0) {
          this.shortlist.id = shortlist.entry[0].resource.id
          setTimeout(() => {
            this.getInterviewAssessment()
          }, 1000);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getInterviewAssessment() {
      let params = {
        _profile: "http://ihris.org/fhir/StructureDefinition/post-interview-profile",
        jobapplication: "Basic/" + this.queries.id,
        practitioner: this.practitionerId
      }
      fetch("/fhir/Basic?" + querystring.stringify(params)).then(response => response.json().then((assessment) => {
        if(assessment.entry && assessment.entry.length > 0) {
          this.assessment.id = assessment.entry[0].resource.id
          setTimeout(() => {
            this.getHiringDecision()
          }, 1000);
        } else {
          this.overlayUnclosed = false
        }
      }))
    },
    getHiringDecision() {
      let params = {
        _profile: "http://ihris.org/fhir/StructureDefinition/hiring-decision-profile",
        jobapplication: "Basic/" + this.queries.id,
        practitioner: this.practitionerId
      }
      fetch("/fhir/Basic?" + querystring.stringify(params)).then(response => response.json().then((hiring) => {
        if(hiring.entry && hiring.entry.length > 0) {
          this.hiring.id = hiring.entry[0].resource.id
          this.overlayUnclosed = false
        } else {
          this.overlayUnclosed = false
        }
      }))
    }
  },
  computed: {
    canAddShortlist() {
      if(!this.shortlist.id) {
        return true
      }
      return false
    },
    canAddAssessment() {
      if(this.shortlist.id && !this.assessment.id) {
        return true
      }
      return false
    },
    canAddHiring() {
      if(this.assessment.id && !this.hiring.id) {
        return true
      }
      return false
    }
  },
  created() {
    this.overlayUnclosed = true
    this.getApplication()
  },
  components: {
    "bw-page-view": () => import("@/site/view/bw-page-view"),
  }
}
</script>